import { ReactElement } from "react"
import { HEADER_HEIGHT } from "@rp/common/settings"
import { RPThemeProps } from "@rp/react/theme"
import { Grid, SxProps, styled } from "@rp/react/web/mui/core"

type BaseMobileHeaderProps = {
  className?: string
  leftContent?: ReactElement | null
  centerContent?: ReactElement | null
  rightContent?: ReactElement | null
  leftColumns?: number
  centerColumns?: number
  rightColumns?: number
  centered?: boolean
  sx?: SxProps<RPThemeProps>
}

const BaseMobileHeaderStruct = ({
  className,
  leftContent,
  centerContent,
  rightContent,
  leftColumns = 3,
  centerColumns = 6,
  rightColumns = 3,
  centered = false,
  sx,
}: BaseMobileHeaderProps) => {
  return (
    <Grid
      sx={sx}
      container
      className={className}
      columnGap={1}
      component="header"
      alignItems="center"
    >
      {leftColumns !== 0 ? (
        <Grid item xs={leftColumns} marginLeft={2}>
          {leftContent}
        </Grid>
      ) : null}
      {centerColumns !== 0 ? (
        <Grid
          item
          xs={centerColumns}
          container={centered}
          justifyContent={centered ? "center" : undefined}
        >
          {centerContent}
        </Grid>
      ) : null}
      {rightColumns !== 0 ? (
        <Grid
          item
          xs={rightColumns}
          marginRight={2}
          marginLeft={1.5}
          container
          justifyContent="right"
        >
          {rightContent}
        </Grid>
      ) : null}
    </Grid>
  )
}

const BaseMobileHeader = styled(BaseMobileHeaderStruct)(({ theme }) => ({
  position: "sticky",
  zIndex: 10,
  top: 0,
  height: HEADER_HEIGHT + 1,
  transform: "translateY(-1px)",
  borderBottom: `1px solid ${theme.palette.grey.A700}`,
  marginBottom: -1,
  backgroundColor: theme.palette.surface.main,
  flexWrap: "nowrap",
  flexShrink: 0,
  "& button": {
    padding: 0,
  },
  "& a": {
    color: theme.palette.text.primary,
    textDecoration: "none",
  },
}))

export { BaseMobileHeader }
export type { BaseMobileHeaderProps }
